import { securedAxiosInstance } from "../axiosConfig";

export async function fileUpload(file) {
  const formdata = new FormData();
  formdata.append("files", file);

  return await securedAxiosInstance.post("/upload", formdata);
}

export async function fileUploadAndLink(file, ref, refId, field) {
  const formdata = new FormData();
  formdata.append("ref", ref);
  formdata.append("refId", refId);
  formdata.append("field", field);
  formdata.append("files", file);

  return securedAxiosInstance.post("/upload", formdata);
}

export async function uploadResumeAndLink(file, index) {
  if (!file) return;

  const uploadResponse = await fileUpload(file);

  const linkResumeResponse = await securedAxiosInstance.put(
    "/students/resume",
    {
      uploadId: uploadResponse.data[0]._id,
      index,
    }
  );
  return linkResumeResponse;
}

export async function deleteResume(uploadId) {
  if (!uploadId) return;

  return await securedAxiosInstance.put(`/students/resume/${uploadId}`);
}
